import { useContext } from "react";
import styled from "@emotion/styled";
import api from "../api";

// Components
import MainLayout from "../layouts/MainLayout";
import Product from "../components/index/Product";

// State
import ProductContext from "../state/product/context";

const DetailsPage = ({ productId }) => {
  const productContext = useContext(ProductContext);
  const product = productContext.productState.products.find(x => x._id === productId);
  console.log(product)
  return (
    <MainLayout>
      <ImgContainer>
        <img src={product.img.hdUrl}></img>
      </ImgContainer>
      <SpecContainer>
        <h2>product.name</h2>
        <h3>product.category</h3>
        <h5>{`$ ${product.cost}`}</h5>
      </SpecContainer>
    </MainLayout>
  );
};

const ImgContainer = styled.div`
  grid-column: 1 / 3;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  img {
    object-fit: cover;
    border: 4px solid black;
  }
`;

const SpecContainer = styled.div`
  grid-column: 3 / 4;
  background-color: red;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;

  * {
    margin: 2vh 0;
  }
`;

DetailsPage.getInitialProps = async router => {
  let productId = router.query.id;

  return { productId };
};

export default DetailsPage;
